* {
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  color: #564a4b;
  background-color: #dbb8b2;
  padding: 2rem;
  font-family: sans-serif;
  font-size: 1.4rem;
}

.calendar {
  background-color: #efded4;
  padding: 2rem;
}

.calendar__month {
  background-color: #f5f5f5;
  align-items: center;
  margin-bottom: 2rem;
  padding: 2rem;
  font-size: 1.6rem;
  display: flex;
}

.calendar__month .month__button {
  margin-left: auto;
}

.calendar__month .month__button--previous, .calendar__month .month__button--next {
  all: unset;
  text-align: center;
  color: #564a4b;
  cursor: pointer;
  background-color: #dbb8b2;
  border-radius: 4px;
  width: 8rem;
  height: 4rem;
  font-size: 1.8rem;
  transition: all .2s;
}

.calendar__month .month__button--previous:hover, .calendar__month .month__button--next:hover {
  color: #f5f5f5;
  background-color: #e2c6c1;
}

.organiser {
  grid-template-rows: 6rem 1fr;
  grid-template-columns: 6rem 1fr;
  display: grid;
}

.organiser__days {
  grid-area: 1 / 1 / auto / -1;
  grid-template-columns: 6rem repeat(7, 1fr);
  display: grid;
}

.organiser__days .day {
  color: #faf0f0;
  background-color: #dd7b30;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  display: flex;
}

.organiser__weeks {
  grid-area: 2 / 1;
  grid-template-rows: repeat(6, 1fr);
  display: grid;
}

.organiser__weeks .week {
  color: #faf0f0;
  background-color: #ebaf8d;
  justify-content: center;
  align-items: center;
  display: flex;
}

.organiser__weeks .week span {
  display: inline-block;
  transform: rotate(-90deg);
}

.organiser__dates {
  grid-area: 2 / 2;
  grid-template-rows: repeat(6, 1fr);
  grid-template-columns: repeat(7, 1fr);
  display: grid;
}

.tasks {
  background-color: #f5f5f5;
  grid-template-rows: repeat(7, 1fr);
  grid-template-columns: 4rem 1fr;
  display: grid;
}

.tasks__date {
  color: #fff;
  text-align: center;
  background-color: #eda541;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: 1.8rem;
  font-weight: 700;
}

.tasks__date--current {
  color: #fff;
  text-align: center;
  background-color: #c23048;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: 1.8rem;
  font-weight: 700;
}

.tasks__divider {
  background-color: #f3d0b2;
  justify-content: flex-end;
  align-items: center;
  gap: 1rem;
  padding-right: 1rem;
  display: flex;
}

.tasks__btn {
  all: unset;
  color: #fff;
  cursor: pointer;
  background-color: #c5a6a0;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  font-size: 1.4rem;
  transition: all .2s;
  display: flex;
}

.tasks__btn:hover {
  color: #dbb8b2;
  background-color: #f5f5f5;
}

.tasks__checkbox {
  grid-template-columns: auto;
  place-content: center;
  font-family: sans-serif;
  font-size: 1.4rem;
  font-weight: 700;
  line-height: 1.1;
  display: grid;
}

.tasks__checkbox--disabled {
  color: var(--form-control-disabled);
  cursor: not-allowed;
}

.tasks__checkbox input[type="checkbox"] {
  appearance: none;
  font: inherit;
  color: #dbb8b2;
  background-color: #f5f5f5;
  border: 2px solid #dbb8b2;
  border-radius: 4px;
  place-content: center;
  width: 2rem;
  height: 2rem;
  margin: 0;
  display: grid;
}

.tasks__checkbox input[type="checkbox"]:before {
  content: "";
  background-color: CanvasText;
  width: 1rem;
  height: 1rem;
  transition: transform .2s ease-in-out;
  transform: scale(0);
  box-shadow: inset 1rem 1rem #dbb8b2;
}

.tasks__checkbox input[type="checkbox"]:checked:before {
  transform: scale(1);
}

.tasks__checkbox input[type="checkbox"]:disabled {
  --form-control-color: #a0a0a0;
  color: #a0a0a0;
  cursor: not-allowed;
}

.tasks__todo {
  color: #564a4b;
  border-bottom: 1px solid #e7e7e7;
  border-right: 1px solid #e7e7e7;
  align-self: center;
  font-size: 1.6rem;
}

.current-day {
  box-shadow: 0 0 0 8px #af938e;
}

.padding {
  background-color: #efded4;
}

.modal__new-task, .modal__edit-task, .modal__delete-task, .modal__weather, .modal__profile {
  z-index: 200;
  background-color: #efded4;
  border: 2rem solid #f5f5f5;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  row-gap: 2rem;
  padding: 4rem 8rem;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 16px 16px #0003;
}

.modal__title {
  margin-bottom: 2rem;
}

.modal__form {
  flex-direction: column;
  display: flex;
}

.modal__task-label {
  margin-bottom: .6rem;
}

.modal__task-label--edit, .modal__task-label--delete {
  align-self: center;
}

.modal__profile-label {
  margin-top: 2rem;
  margin-bottom: .6rem;
}

.modal__profile-label:first-of-type {
  margin-top: 0;
}

.modal__task-input, .modal__profile-input {
  all: unset;
  background-color: #f5f5f5;
  border-bottom: 2px solid #dbb8b2;
  width: 24rem;
  padding: 1rem;
}

.modal__buttons {
  gap: 2rem;
  margin-top: 2rem;
  display: flex;
}

.modal__btn-save, .modal__btn-profile-save, .modal__btn-edit, .modal__btn-cancel, .modal__btn-done, .modal__btn-ok, .modal__btn-delete {
  all: unset;
  text-align: center;
  color: #564a4b;
  cursor: pointer;
  background-color: #dbb8b2;
  border-radius: 4px;
  width: 8rem;
  height: 4rem;
  padding: 0 2rem;
  font-size: 1.8rem;
  transition: all .2s;
}

.modal__btn-save:hover, .modal__btn-profile-save:hover, .modal__btn-edit:hover, .modal__btn-cancel:hover, .modal__btn-done:hover, .modal__btn-ok:hover, .modal__btn-delete:hover {
  background-color: #ebaf8d;
}

.modal__backdrop {
  z-index: 100;
  background-color: #0009;
  width: 100%;
  height: 100%;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.modal__task-container {
  grid-template-columns: 1fr 3fr 1fr;
  margin-bottom: 1rem;
  display: grid;
}

.task-text {
  background-color: #f5f5f5;
  padding: 1rem 2rem;
  display: inline-block;
}

.edit-task-btn, .delete-task-btn {
  all: unset;
  text-align: center;
  color: #564a4b;
  cursor: pointer;
  background-color: #dbb8b2;
  border-radius: 4px;
  font-size: 1.8rem;
  transition: all .2s;
}

.edit-task-btn:hover, .delete-task-btn:hover {
  background-color: #ebaf8d;
}

.weather__img {
  width: 20rem;
  height: 20rem;
}

.weather__temp {
  margin-bottom: 4rem;
  font-size: 6rem;
  font-weight: 700;
}

.weather__info {
  justify-content: space-between;
  gap: 12rem;
  width: 100%;
  display: flex;
}

.weather__humidity, .weather__wind {
  gap: 1rem;
  display: flex;
}

.humidity, .wind {
  width: 6rem;
  height: 6rem;
}

.humidity__text, .wind__text {
  align-self: center;
  font-size: 3rem;
  font-weight: 700;
}

.header {
  grid-template-columns: repeat(3, 1fr);
  margin-bottom: 1rem;
  display: grid;
}

.profile {
  grid-column: 1;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: auto 1fr auto;
  column-gap: 2rem;
  display: grid;
}

.profile__avatar {
  cursor: pointer;
  grid-area: 1 / 1 / -1;
  align-self: center;
  position: relative;
}

.profile__avatar:after {
  content: "";
  background-color: #efded4;
  background-image: url("gear-solid.96bb4e34.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 2px solid #efded4;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  display: inline-block;
  position: absolute;
  bottom: 0;
  right: 0;
}

.profile__avatar:hover:after {
  animation-name: spin;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.profile__avatar-img {
  border-radius: 50%;
  width: 10rem;
  height: 10rem;
}

.profile__username {
  align-self: flex-end;
  font-size: 1.6rem;
  font-weight: 700;
}

.profile__location {
  align-self: flex-start;
  font-size: 1.6rem;
}

.profile__weather {
  grid-area: 1 / 3 / -1;
  align-items: center;
  display: flex;
}

.profile__weather-img {
  width: 10rem;
  height: 10rem;
}

.profile__weather-text {
  font-size: 2.4rem;
  font-weight: 700;
}

.profile__title {
  text-align: center;
  color: #fff;
  grid-column: 2;
  padding: 2rem;
  font-family: Tangerine, cursive;
  font-size: 3.6rem;
}

@keyframes spin {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.spinner-class {
  animation-name: spin;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

