$color-left-light: #df998d;
$color-left-medium: #de8b83;
$color-left-dark: #d17f81;
$color-left-text-light: #faf0f0;
$color-left-text-medium: #f6e6e6;
$color-left-text-dark: #cc887b;

$color-right-light: #f5f5f5;
$color-right-medium: #efded4;
$color-right-dark: #dbb8b2;

$color-calendar-light: #f5f5f5;
$color-calendar-dark: #f3d0b2;

$color-background-day: #dd7b30;
$color-background-date: #eda541;
$color-background-week: #ebaf8d;

$color-text-completed: #d7babc;
$color-text-task: #564a4b;

$color-divider: #e7e7e7;

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  font-family: sans-serif;
  font-size: 1.4rem;
  color: $color-text-task;
  background-color: $color-right-dark;
  padding: 2rem;
}

.calendar {
  background-color: $color-right-medium;
  padding: 2rem;

  &__month {
    display: flex;
    align-items: center;
    font-size: 1.6rem;
    background-color: $color-right-light;
    padding: 2rem;
    margin-bottom: 2rem;

    .month__button {
      margin-left: auto;

      &--previous,
      &--next {
        all: unset;
        width: 8rem;
        height: 4rem;
        text-align: center;
        font-size: 1.8rem;
        color: $color-text-task;
        border-radius: 4px;
        background-color: $color-right-dark;
        cursor: pointer;
        transition: all 0.2s;

        &:hover {
          color: $color-right-light;
          background-color: #e2c6c1;
        }
      }
    }
  }
}
